/* ROOT LAYOUT */
body {
  font-family: "Comic Sans MS", sans-serif;
  background: linear-gradient(135deg, #ffd86f, #ff9ec4, #b38bff);
  background-size: 300% 300%;
  animation: gradientMove 10s ease infinite;
}

@keyframes gradientMove {
  0% {background-position: 0% 50%;}
  50% {background-position: 100% 50%;}
  100% {background-position: 0% 50%;}
}

.ks-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  padding: 18px;
  text-align: center;
}

.ks-title {
  font-size: 3rem;
  margin-bottom: 8px;
}

.hud {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 10px;
}

.stat-box {
  background: #fff2cc;
  border: 2px solid #d1a000;
  border-radius: 10px;
  padding: 6px 14px;
  font-size: 1.2rem;
}

/* Character */
.character-img {
  height: 200px;
  max-height: 33vh;
  width: auto;
  margin: 10px auto;
  display: block;
  image-rendering: auto;
}

/* Word + Speaker */
.word-row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.word-display {
  font-size: 3rem;
  letter-spacing: 10px;
}

.speaker-btn {
  width: 52px;
  height: 52px;
  font-size: 1.6rem;
  border-radius: 50%;
  border: 3px solid #cfa000;
  background: #fff8cc;
  cursor: pointer;
  transition: transform 0.15s ease;
}

.speaker-btn:hover { transform: scale(1.15); }
.speaker-btn:active { transform: scale(0.92); }

/* Definition */
.definition-box {
  background: white;
  border-radius: 12px;
  padding: 12px;
  font-size: 1.35rem;
  width: 90%;
  margin: 10px auto 20px;
}

/* Tiles */
.tile-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 14px;
}

.letter-tile {
  width: 70px;
  height: 70px;
  font-size: 2.2rem;
  font-weight: bold;
  border-radius: 12px;
  cursor: pointer;
  background: #ffe9b6;
  border: 3px solid #a57c21;
  transition: transform .15s;
}

.letter-tile:hover { transform: scale(1.15); }
.letter-tile:active { transform: scale(0.90); }

.undo-btn {
  margin-top: 10px;
  font-size: 1.5rem;
  padding: 10px 24px;
  border: none;
  background: #cdefff;
  border-radius: 10px;
  cursor: pointer;
}

.game-over-box {
  background: #fff8db;
  padding: 24px;
  border-radius: 20px;
  font-size: 2rem;
}

.restart-btn {
  font-size: 1.7rem;
  margin-top: 12px;
  padding: 10px 28px;
  border-radius: 10px;
  background: #ffd566;
  border: 3px solid #b48b00;
  cursor: pointer;
}

@media (max-width: 600px) {
  .character-img {
    height: 160px;
  }
  .word-display {
    font-size: 2.2rem;
    letter-spacing: 6px;
  }
  .letter-tile {
    width: 55px;
    height: 55px;
    font-size: 1.7rem;
  }
}



